.container {
  max-width: 480px;
  margin: 0 auto;
  padding-left: 16px;
  padding-right: 16px;

  @media screen and (min-width: 767.98px) {
    max-width: 768px;
  }

  @media screen and (min-width: 1279.98px) {
    max-width: 1280px;
  }
}

