  height: 16px;
  width: 16px;
  background-color: #4b48db;
  border-radius: 50%;
  transition: all .5s;
  position: absolute;
  top: 1px;
  left: 1px;
}

.checkbox-header__input:checked + .checkbox-header__switch-slider {
