:root {
  --toggle-light: #aeb3cb;
  --toggle-dark: linear gradient #378fe6 to #3eda82;
}

.switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
}

.slider {
  cursor: pointer;
  background-color: var(--toggle-light);
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 16px;
  width: 16px;
  background-color: #fff;
  transition: all .4s;
  position: absolute;
  bottom: 2px;
  left: 24px;
}

input:checked + .slider {
  background: linear-gradient(90grad, #378fe6, #3eda82);
}

input:checked + .slider:before {
  transform: translateX(-24px);
}

.slider, .round {
  border-radius: 34px;
}

.round:before {
  border-radius: 50%;
}

input:checked + .round:before {
  background-color: #1e202a;
}

